.advantages__box {
  text-transform: uppercase;
}

.advantages__header {
  text-align: center;
  @media (min-width: @tablet-width) {
    padding: 0 124px 0 124px;
  }

  @media (min-width: @desktop-width) {
    padding: 0 300px 0 327px;
  }
}

.advantages__slogan {
  font-weight: 700;
  font-size: 18px;
  margin: 0;
  padding-top: 32px;
  padding-bottom: 35px;

  @media (min-width: @tablet-width) {
    font-size: 21px;
    line-height: 26px;

    br {
      display: none;
    }
  }
}

.advantages__intro {
  color: @brown-text;
  margin: 0;
  padding-bottom: 53px;

  @media (min-width: @tablet-width) {
    br {
      display: none;
    }
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.advantages__list--sedona-city {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  padding: 23px 18px;
  text-align: start;

  @media (min-width: @tablet-width) {
    padding: 0;
    display: flex;
    flex-direction: row;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-direction: row;
  }
}

.advantages__item {
  &--grey-background {
    background-color: @grey-background;
    position: relative;
    display: flex;
  }
}

.advantages__item--grey-background:not(:last-child) {
  ::before {
    content: "";
    position: absolute;
    width: 284px;
    height: 1px;
    left: 18px;
    bottom: 0;
    background-color: #d6d6d6;

    @media (min-width: @tablet-width) {
      display: none;
    }
  }
}

.advantages__item--town {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-column: 1 / 4;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-column: 1 / 4;
  }
}

.advantages__item--sedona-city {
  display: grid;
  grid-template-columns: 40px auto;
  column-gap: 7px;
  grid-template-rows: 40px min-content;
  row-gap: 10px;
  justify-items: start;
  align-items: center;

  @media (min-width: @tablet-width) {
    br {
      display: none;
    }

    display: block;
    text-align: center;
    padding: 157px 33px 58px 33px;
  }

}

  .advantages__item--housing {
    position: relative;
    ::before {
      content: "";
      position: absolute;
      width: 40px;
      height: 40px;
      top: 0;
      left: 0;
      background-image: url("../images/index/house-icon.svg");

      @media (min-width: @tablet-width) {
        top: 100px;
        left: 110px;
      }
    }
  }
.advantages__item--food {
  padding-top: 25px;
  padding-bottom: 25px;
  position: relative;
  ::before {
    content: "";
    position: absolute;
    width: 40px;
    height: 40px;
    top: 25px;
    left: 0;
    background-image: url("../images/index/burger-icon.svg");
  }

  @media (min-width: @tablet-width) {
    padding: 158px 33px 60px 33px;
  }
}

.advantages__item--souvenirs {
  position: relative;
  ::before {
    content: "";
    position: absolute;
    width: 40px;
    height: 40px;
    top: 0;
    left: 0;
    background-image: url("../images/index/gift-icon.svg");
  }
}

.advantages__item--bridge {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-column: 1 / 4;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-column: 1 / 4;
  }
}

.advantages__title--sedona-city {
  grid-column: 2 / 3;

  @media (min-width: @tablet-width) {
    padding-bottom: 30px;
  }
}

.advantages__description--sedona-city {
  grid-column: 2 / 3;
}

.advantages__title {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.advantages__title--blue-background {
  padding-bottom: 11px;
}

.advantages__title--grey-background {
  padding-bottom: 11px;
}

.advantages__description {
  color: @brown-text;
  margin: 0;

  @media (min-width: @tablet-width) {
    br {
      display: none;
    }
  }

  &--blue-background {
    color: @white-color;
    padding-top: 17px;
  }
}

.advantages__description--grey-background {
  padding-top: 17px;
}

.advantages__photo {
  vertical-align: middle;
}

.advantages__photo--bridge {
  @media (min-width: @tablet-width) {
    order: -1;
  }

  @media (min-width: @desktop-width) {
    order: -1;
  }
}

.advantages__container {
  &--blue-background {
    color: @white-color;
    background-color: @blue-background;

    @media (min-width: @tablet-width) {
      display: flex;
      align-items: center;
    }

    @media (min-width: @desktop-width) {
      display: flex;
      align-items: center;
    }
  }
}

.advantages__wrapper {
  padding: 33px 18px 40px 18px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-direction: column;
  }
}

.advantages__item--grey-background .advantages__wrapper {
  padding: 24px 26px;
  @media (min-width: @tablet-width) {
    padding: 52px 33px;
  }
}

.advantages__item--grey-background:nth-child(2n) .advantages__wrapper {
  @media (min-width: @tablet-width) {
    padding: 41px 33px;
  }
}
